main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

main h3 {
    margin: 1rem 0;
}

/* */
div.logo {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
}
div.logo img {
    margin-right: 1.5rem;
}
div.logo h1 {
    color: #ffe66e;
}

/* */
div.intro {
    text-align: center;
    margin: 2rem 0;
    font-size: 1.3rem;
}

div.intro > span {
    display: block;
}


/* */
div.collection {
    display: flex;
    flex-direction: row;
    text-align: left;
    margin: 2rem 0;
    font-size: 1.1rem;
}

div.collection > div.rarity {
    margin-right: 4rem;
    padding-right: 4rem;
    border-right: 1px solid #ffe66e66;
}

div.collection > div.rarity > strong {
    color: #ffe66e;
}

div.collection > div.rarity > ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

div.collection > div.rarity > ul li {
    margin: 0;
    padding: 0;
}

div.collection > div.rarity-gif {
    margin-right: 2rem;
}

/* */
h3 {
    font-weight: normal;
}

div.collection > div.minting {
    text-align: left;
    text-transform: uppercase;
    font-weight: normal;
}

div.collection > div.minting p {
    margin: 0;
    padding: 0;
}

div.collection > div.minting > p > strong {
    color: #ffe66e;
}

